
.intro-con {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.title {
	font-family: "Abel", sans-serif;
	font-size: 50px;
	line-height: 40px;
	margin-bottom: 20px;
}

.moto {
	font-family: "Open Sans", sans-serif;
	font-size: 18px;
	line-height: 20px;
}

.links {
	text-align: center;
	margin-top: 15px;
}

.links > a {
	display: block;
	font-weight: bold;
	margin-top: 10px;
	margin-bottom: 10px;
	text-decoration: underline;
	text-transform: uppercase;
	font-size: 19px;
	-webkit-filter: brightness(0.9);
	        filter: brightness(0.9);
	tranition: all 100ms ease-in;
}

.links > a:hover {
	color: white;
	-webkit-filter: brightness(2);
	        filter: brightness(2)
}

.footer {
	position: absolute;
	bottom: 10px;
	right: 10px;;
}

.viewers {
	position: absolute;
	bottom: 10px;
	left: 10px;;
}

* {
	padding-top: 0;
}



